import CanvasDraw from 'react-canvas-draw';
import { useState, useRef } from 'react';
import '../css/canvas.css';
import Spody from './Spody';
import Jersey from './Jeresy';
import img from '../img/favicon.png' 
import { toPng } from 'html-to-image';

const savePNG = () => {
    const area = document.querySelector("#savePng");
    toPng(area
        // , { canvasWidth: 1080, canvasHeight: 1080 }
        )
        .then(function (dataUrl) {
            const download = document.createElement('a');
            download.href = dataUrl;
            download.download = "Spodify Dolan.png";
            download.click();
        })
        .catch(function (error) {
            console.error('oops, something went wrong!', error);
        });
};

const Canvas = () => {


    const [config, setConfig] = useState({
        Radius: 5,
        Brushcolor: '#000000',
        Bg: ''
    });

    const [spodyConfig, setSpodyConfig] = useState({
        Skin: "#FED1D0",
        Tshirt: "#2875FF",
        Eye: "#ffffff",
        Border: "#121212",
        Display: "block"
    });

    const [jersey, setJersey] = useState("https://raw.githubusercontent.com/kvnal/spodify-jersey-api/main/assets/Spodify.png");
    const [skin,setSkin] = useState('#E69093')
    const canvasRef = useRef(null);
    // fetch_()
    return (

        <div className="container">
            <div className="playground" >
                <h2>Playground</h2>

                <div className="brush-view">
                    <div className="brush-preview" style={{ height: `${config['Radius'] * 2}px`, width: `${config['Radius'] * 2}px`, backgroundColor: config['Brushcolor'] }}>
                    </div>

                </div>

                <div className="Brush">
                    <span>Radius</span>
                    <input type="range" min="1" max="50" defaultValue={config['Radius']} onChange={(e) =>
                        setConfig({ ...config, Radius: parseInt(e.target.value) })} />
                    <div className="colorpicker">
                        <span>Brush Color</span>
                        <input type="color" defaultValue={config['Brushcolor']} onChange={(e) => setConfig({ ...config, Brushcolor: e.target.value })} />
                    </div>

                    <div className="spody">
                        <span className="spody-title">Spody</span>
                        <div className="colorpicker">
                            <span>Skin</span>
                            <input type="color"
                                defaultValue={skin}
                                value={skin}
                                onChange={(e) => setSkin(e.target.value)} />
                            
                        </div>
                        
                        <div className="colorpicker">
                            <span>Background Image <strong>(BETA)</strong></span>
                            <input type="file"
                                onChange={(e)=>{
                                    var reader = new FileReader();
                                    reader.onload= (event)=>{
                                        // console.log();
                                        setConfig({...config,Bg:event.target.result})


                                    }
                                    reader.readAsDataURL(e.target.files[0])
                                }}
                            />
                        </div>


                        <div className="colorpicker ">
                            {spodyConfig.Display === "block" && <button className="hide-show"
                                onClick={() => {
                                    setSpodyConfig({ ...spodyConfig, Display: "none" });
                                }
                                } >Hide</button>}


                            {spodyConfig.Display === "none" && <button className="hide-show"
                                onClick={() => {
                                    setSpodyConfig({ ...spodyConfig, Display: "block" });
                                }
                                } >Show</button>}

                            <div className="save-button">
                                <button onClick={() => savePNG()}>Save</button>
                            </div>

                            <div className="github">
                                <a href="https://github.com/kvnal">@github.com/kvnal</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <div className="canvas-container">


                <div className="canvas-control">
                    <button className="hide-show" onClick={() => canvasRef.current.undo()} >↩ Undo</button>
                    <button className="hide-show" onClick={() => canvasRef.current.clear()} >⊗ Clear</button>
                    <button className="hide-show" onClick={() => {
                        window.location.reload();
                        // canvasRef.current.clear()
                        // setSpodyConfig({...resetConfig})}
                    }}>↻ Reset</button>
                </div>
                <div id="savePng" >
                {/* style={{ background: `url(${config["Bg"]})`}} */}
                <div className="save" id="save" >

                        <div className="canvas">
                            <CanvasDraw ref={canvasRef}
                                canvasHeight={540}
                                canvasWidth={540}
                                brushColor={config['Brushcolor']} lazyRadius={0}
                                brushRadius={config['Radius']} hideGrid={true}
                                catenaryColor={''}
                            
                            />

                        </div>

                        
                        <div className="spody-canvas">
                            {/* <div className="svg"> */}
                            <div className="spody-bg">

                            </div>
                            <div className="backgroundImg" style={{backgroundImage : `url(${config["Bg"]})`, backgroundSize:"cover",backgroundPosition:"center"}}>
                            </div>
                            <div className="svg" style={{ display: spodyConfig['Display']}}>
                                <Spody Skin={skin} />
                            </div>
                            
                            <div className="spody-jersey">
                                <img src={jersey} />

                            </div>
                        </div>
                    </div>
                </div>


            </div>

            <div>
                <Jersey setJersey={setJersey} setSkin={setSkin} />
            </div>
        </div>

    );
};

export default Canvas;