import favicon from '../img/favicon.png';
const Nav = () => {
    return (

        <div>

            <div className="nav" >
                <div onClick={() => window.location.reload()}>

                    <img src={favicon} alt="" />
                    <h1>Spodify</h1>
                    <span>Jersey - Edition</span>
                    <span className="beta">(Beta)</span>
                </div>

            </div>
            <div className="notification">
                <span>Customize your Dolan Face from <a href="https://kvnal.github.io/spodify/">Here! (Spodify)</a> </span>
            </div>
        </div>

        

    );
}

export default Nav;