import { useEffect, useState } from "react";

const useFetchJersey = (url,setJersey) => {

        const [jersey,setjersey]=useState(null);
        const [loading, setLoading] = useState(true)
        const [random, setRandom] = useState("")

        useEffect(()=>{
            fetch(url)
            .then(res=>res.json())
            .then(data =>{
                setjersey(data.jersey)
                setLoading(false)
                setRandom(data.jersey[Math.floor(Math.random()*data.jersey.length)].url)
                // console.log("usefetch >");
                // console.log(jersey);
                // setJersey(data.jersey[Math.floor(Math.random()*data.jersey.length)].url)
            })
        },[])
        return {loading,jersey,random};
     
}
 
export default useFetchJersey;