import { useEffect, useState } from 'react';
import '../css/jersey.css';
import ban from '../svg/ban.svg';
import cart from '../svg/cart.svg';
import useFetchJersey from './FetchJersey';

const url = "https://raw.githubusercontent.com/kvnal/spodify-jersey-api/main/main.json";
const Jersey = ({ setJersey ,setSkin}) => {
    const { loading, jersey , random} = useFetchJersey(url,setJersey);
    const [once,setOnce] = useState(true)

    return (
        <div>

            <div className="jersey-container">

                {loading && <h1>Loading...</h1>}
                {jersey &&
                    <div className="jersey">
                        
                        <div className="Tshirts">
                            
                            <img src={ban} alt="None"
                                onClick={() => { setJersey(null); }} />
                            <span>None</span>

                        </div>
                            {
                        jersey.map((element) => (
                            
                            <div className="Tshirts" key={element.id}
                                onClick={() => {
                                    setJersey(element.url);
                                    setSkin(element.skin)
                                    // console.log(element.url)
                                }
                                } >

                                <img src={element.url} alt={element.name} />
                                <span>{element.name}</span>
                            </div>
                        ))
                        
                        }
                        
                        <div className="Tshirts">
                            <img src={cart} height="40px" alt="None" />
                            <span style={{color:"#666666"}}>Coming Soon</span>
                        </div>
                    </div>
                    
                    
                }
            </div>
            <span>👕 Jerseys designed by <a href="https://instagram.com/dolanjerseys">@dolanjerseys</a></span>
        </div>
    );
};

export default Jersey;
